<template>
  <div class="con">
    <publicTop></publicTop>
    <div class="home-top">
      <div class="home-top-l" @click="goB">
        <img src="@/assets/left-le.png" />
      </div>
      <div class="home-top-r">
        <!-- <img
          src="@/assets/icon_collect.png"
          class="home-top-r-img1"
          v-if="!contentDetail.flag"
        />
        <img src="@/assets/icon_collected.png" class="home-top-r-img1" v-else /> -->
        <!-- <img
          src="@/assets/icon_share.png"
          class="home-top-r-img2"
          @click="goShare"
        /> -->
        详情
      </div>
    </div>

    <div class="video-div">
      <video
        :src="contentDetail.videoSrc"
        controls="controls"
        id="video"
        :poster="contentDetail.imageSrc"
      ></video>
    </div>
    <div class="home-body">
      <div class="list-all-li-end">
        <div class="list-all-li-end-l">
          来源于{{ contentDetail.publisher }}
          <span>{{ contentDetail.hits }}次浏览数</span>
        </div>
        <div class="list-all-li-end-r">{{ contentDetail.releaseDate }}</div>
      </div>
      <!-- <div class="home-body-content">{{ dec }}</div> -->
    </div>

    <!-- 底部 -->
    <!-- <div class="ending">
      <div class="ending-l">
        <img src="@/assets/icon_edit.png" />
        <input type="text" v-model="inputContent" placeholder="写下互动留言…" />
      </div>
      <div class="ending-m">
        <img src="@/assets/icon_mess.png" />
        <div>245</div>
      </div>
      <div class="ending-r">
        <img src="@/assets/icon_like.png" />
        <div>1w+</div>
      </div>
    </div> -->
  </div>
</template>

<script>
import date from "@/utils/date.js";
import publicTop from "@/components/publicTop.vue";
import qs from "qs";
export default {
  name: "videoDetail",
  components: {
    publicTop,
  },
  data() {
    return {
      inputContent: "",
      contentDetail: {},
      newsId: "",
      dec: "9月19日，“北京林业大学工学院干果公司”产学研合作基地",
      videoSrc:
        "http://wxsnsdy.tc.qq.com/105/20210/snsdyvideodownload?filekey=30280201010421301f0201690402534804102ca905ce620b1241b726bc41dcff44e00204012882540400&bizid=1023&hy=SH&fileparam=302c020101042530230204136ffd93020457e3c4ff02024ef202031e8d7f02030f42400204045a320a0201000400",
    };
  },
  methods: {
    goB() {
      this.$router.go(-1);
    },
    getDetailContent() {
      const params = {
        contentId: this.newsId,
      };
      console.log(params);
      this.$axios
        // .get("/f/newsCenter/ajax_movie_view?contentId=" + this.newsId)
        .post("/f/newsCenter/ajax_movie_view", qs.stringify(params))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            this.contentDetail = res.data.data.movie;
            res.data.data.movie.releaseDate = date
              .formatTimeTwo(res.data.data.movie.releaseDate / 1000, "Y-M-D")
              .slice(0, 10);
          } else {
            this.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    // console.log(this.$store.getters.getNewsId, "89");
    // this.newsId = this.$store.getters.getNewsId;
    this.newsId = this.$route.query.id;
    this.getDetailContent();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #ffffff;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.home-top {
  width: 7.5rem;
  padding: 0rem 0.4rem;
  box-sizing: border-box;
  height: 0.88rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #ffffff;
  border-bottom: 0.01rem solid #eeeeee;
}
.home-top-l {
  display: flex;
  align-items: center;
  width: 2rem;
  height: 0.88rem;
  position: absolute;
  top: 0;
  left: 0.4rem;
}
.home-top-l img {
  width: 0.2rem;
  height: 0.3rem;
}

.home-top-r {
  font-family: PingFangSC-Semibold;
  font-size: 0.32rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0rem;
  /* display: flex;
  align-items: center;
  justify-content: space-between; */
}

.home-top-r-img1 {
  width: 0.4rem;
  height: 0.36rem;
  margin-right: 0.44rem;
}

.home-top-r-img2 {
  width: 0.36rem;
  height: 0.38rem;
}

.video-div {
  width: 7.5rem;
  height: 3.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
video {
  width: 7.5rem;
  height: 3.6rem;
  object-fit: fill;
}

.home-body {
  width: 7.5rem;
  padding: 0.4rem 0.32rem;
  box-sizing: border-box;
  background-color: #ffffff;
}
.home-body-t {
  width: 100%;
  font-family: PingFangSC-Medium;
  font-size: 0.32rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #2b2e34;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
  height: 0.9rem;
}
.home-body-content {
  width: 100% !important;
  text-align: left;
  font-family: PingFangSC-Medium;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.44rem;
  letter-spacing: 0rem;
  color: #7a7c8b;
  text-align: justify;
  padding-top: 0.32rem;
  box-sizing: border-box;
}
.home-body-content >>> img {
  max-width: 100% !important;
  height: auto;
}
.home-body-content >>> table {
  max-width: 100% !important;
  height: auto;
}
.home-body-content >>> span {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
}
.home-body-content >>> span > font {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> a {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  word-break: break-all;
  white-space: normal;
}
.home-body-content >>> p {
  max-width: 100% !important;
  height: auto;
}
.home-body-content >>> table {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  table-layout: fixed;
}
.home-body-content >>> table tbody {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
}
.home-body-content >>> table tbody tr {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  overflow-x: scroll;
}
.home-body-content >>> p > img {
  max-width: 100% !important;
  height: auto;
  overflow-x: scroll;
}
.list-all-li-end {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.18rem;
}
.list-all-li-end-l {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #b3b3b3;
}
.list-all-li-end-l span {
  display: inline-block;
  margin-left: 0.2rem;
}
.list-all-li-end-r {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #b3b3b3;
}

.ending {
  width: 7.5rem;
  background-color: #ffffff;
  box-shadow: inset 0rem 0.02rem 0rem 0rem #f0f0f0;
  padding: 0.42rem 0.32rem 0.46rem 0.32rem;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ending-l {
  width: 5.06rem;
  height: 0.64rem;
  background-color: #f8f8f8;
  border-radius: 0.33rem;
  border: solid 0.02rem #f4f6f8;
  padding: 0 0.22rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ending-l img {
  width: 0.3rem;
  height: 0.28rem;
}
.ending-l input {
  width: 4rem;
  height: 0.64rem;
  outline: none;
  border: none;
  text-align: left;
  font-family: PingFangSC-Regular;
  font-size: 0.3rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.64rem;
  letter-spacing: 0rem;
  color: #333333;
  background-color: #f8f8f8;
}
.ending-l input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: rgba(153, 156, 168, 0.8);
}
.ending-m {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  height: 0.64rem;
}
.ending-m img {
  width: 0.3rem;
  height: 0.28rem;
}
.ending-m div {
  font-family: PingFangSC-Regular;
  font-size: 0.2rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.28rem;
  letter-spacing: 0rem;
  color: rgba(122, 124, 139, 0.8);
}
.ending-r {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  height: 0.64rem;
}
.ending-r img {
  width: 0.3rem;
  height: 0.28rem;
}
.ending-r div {
  font-family: PingFangSC-Regular;
  font-size: 0.2rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.28rem;
  letter-spacing: 0rem;
  color: rgba(122, 124, 139, 0.8);
}
</style>


